
























































@import '@design';
.input {
  position: relative;
  display: inline-block;
  width: 75px;

  &__text {
    width: 100%;
    background-color: #f6f6f6;
    border-radius: 40px;
    text-align: center;
    height: 25px;
    line-height: 25px;
  }
  &__button {
    position: absolute;
    width: 25px;
    height: 100%;
    border-radius: 50%;
    text-align: center;
    line-height: 25px;
    cursor: pointer;
    user-select: none;
    box-sizing: border-box;
    &--decrease {
      left: 0;
      background-color: white;
      color: $color-primary;
    }
    &--increase {
      right: 0;
      background-color: $color-primary;
      color: white;
    }
    &--disabled {
      background-color: transparent;
      border: 1px solid white;
      color: white;
    }
  }
}
