



































































































































































@import '@design';
.address {
  padding: 6px 8px;
  color: white;
  border: 1px solid white;
  border-radius: 6px;
  margin: 20px;
  font-size: 0.85rem;
}
.product {
  &__list {
    position: relative;
    padding-bottom: 60px;
    &:before {
      content: '';
      position: absolute;
      width: calc(100% - 60px);
      height: 100%;
      top: 0;
      left: 60px;
      border-top-left-radius: 160px;
      z-index: -1;
      background: linear-gradient(
        rgba(white, 0.9),
        rgba(white, 0.6),
        rgba(white, 0.4)
      );
      border: 1px solid rgba(white, 0.2);
    }
  }

  &__item {
    display: flex;
    align-items: center;
    padding: 0 35px 25px;
  }
  &__image {
    position: relative;
    padding-left: 25px;
    padding-bottom: 15px;
    flex-basis: 100px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 30px;
    img {
      width: 100%;
    }
    &:after {
      content: '';
      background-color: rgba(white, 0.25);
      width: 100px;
      height: 100px;
      position: absolute;
      border-radius: 50%;
      z-index: -1;
      left: 0;
      bottom: 0;
      box-shadow: 10px 10px 35px rgba(#f19040, 0.3);
      backdrop-filter: blur(9px);
    }
  }
  &__info {
    flex-grow: 1;
  }
  &__ingredients {
    color: $color-grey;
    font-size: 0.85rem;
  }
  &__actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  &__price:before {
    content: '$';
    color: $color-grey;
    font-size: 0.85rem;
    padding-right: 3px;
  }
}
.panel {
  position: fixed;
  bottom: 0;
  width: 100%;

  &__payment {
    padding: 20px;
    text-align: center;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    background-color: rgba(white, 0.8);
    box-shadow: 10px 10px 35px rgba(#f19040, 0.3);
    backdrop-filter: blur(10px);
    border-bottom: 1px solid rgba(white, 0.8);
  }
  &__total {
    padding: 16px 20px;
    background-color: rgba(white, 0.8);
    box-shadow: 10px 10px 35px rgba(#f19040, 0.3);
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__price {
    color: $color-primary;
    font-weight: bold;
    font-size: 1.25rem;
    font-style: italic;
    border-bottom: 1px solid #4a4a4a;
  }
}
.methods {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
  &__item {
    border-radius: 50%;
    background-color: rgba(white, 0.7);
    box-shadow: 10px 10px 35px rgba(#f19040, 0.25);
    backdrop-filter: blur(10px);
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0.85rem;
    border: 2px solid transparent;
    transition: border-color $duration-transition-base linear;
    &:hover,
    &--selected {
      border-color: $color-primary;
    }
  }
  &__name {
    margin-top: 8px;
  }
}
